
import $ from "../../../static/jquery.js";
import Swiper from "../../../static/swiper.js";


import XHeader from 'vux/src/components/x-header/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Search from 'vux/src/components/search/index.vue'
import Icon from 'vux/src/components/icon/index.vue'


import swiper from "../../components/swiper/swiper.vue";

import notice from "../../components/notice/notice.vue";

import cate from "../../components/cate/cate.vue";

import goodsCard from "../../components/goodsCard/goodsCard.vue";

import imgThree from "../../components/imgThree/imgThree.vue";

import search from "../../components/search/search.vue";

import scroller from "../../components/scroller/scroller.vue";


export default {
name: "app",
components: {
XHeader,
ViewBox,
swiper,
notice,
cate,
Flexbox,
FlexboxItem,
goodsCard,
Scroller,
imgThree,
Tab,
TabItem,
search,
Icon,
scroller
},
mounted() {
this.update();
this.nav();

this.$get(
this.$serverHome + "goods/getList", {
page: this.currentPage,
limit: this.pageSize
},
res => {
console.log(res);
this.goodsList = res.msg;
}
);


/*
this.$get(
"", {},
res => {
console.log(res);
}
);
*/

var galleryTop = new Swiper(".gallery-top", {
spaceBetween: 10,
onTransitionStart: function(swiper) {
var id = swiper.activeIndex;
console.log(id);
$("#topNav  .active").removeClass("active");
$("[data-index='" + (id + 1) + "']").addClass("active");
mySwiper.slideTo(id - 1, 500);
}
});
var mySwiper = new Swiper("#topNav", {
freeMode: true,
freeModeMomentumRatio: 0.5,
slidesPerView: "auto"
});
var swiperWidth = mySwiper.container[0].clientWidth;
var maxTranslate = mySwiper.maxTranslate();
var maxWidth = -maxTranslate + swiperWidth / 2;
$(".swiper-container").on("touchstart", function(e) {

});
$("#topNav .swiper-slide").on("click", function(e) {

var id = $(this).attr("data-index");
galleryTop.slideTo(id - 1, 0);
});
mySwiper.on("tap", function(swiper, e) {

var slide = swiper.slides[swiper.clickedIndex];
var slideLeft = slide.offsetLeft;
var slideWidth = slide.clientWidth;
var slideCenter = slideLeft + slideWidth / 2;


mySwiper.setWrapperTransition(300);

if(slideCenter < swiperWidth / 2) {
mySwiper.setWrapperTranslate(0);
} else if(slideCenter > maxWidth) {
mySwiper.setWrapperTranslate(maxTranslate);
} else {
var nowTlanslate = slideCenter - swiperWidth / 2;

mySwiper.setWrapperTranslate(-nowTlanslate);
}
$("#topNav  .active").removeClass("active");
$("#topNav .swiper-slide")
.eq(swiper.clickedIndex)
.addClass("active");
});

},
methods: {

toOffline() {
this.$router.push({
path: "/offline/offline",
name: '/offline/offline'
});
},

openNav(group, j, list) {
console.log("haha")
for(var i = 0; i < list.length; i++) {
list[i].isOpen = false;
}
group.isOpen = true;
},

toGoodsInfo(item) {
var goods_id = item.goods_id;
this.$router.push({
path: "/goodsInfo/goodsInfo",
name: '/goodsInfo/goodsInfo',
params: {
goods_id: goods_id
}
});
},
setModel: function(type) {
this.model = type;
},
toSearch() {
this.$router.push({
replace: true,
path: "/search"
});
},

toMessage() {
this.$router.push({
path: "/message/message"
});
},

toAddressSwitch() {
this.$router.push({
path: "/addressSwitch/addressSwitch"
});
},
update() {



if(!this.$route.params["address"]) {
if(!localStorage.address) {
localStorage.address = "北京市";
return;
} else {
this.address = localStorage.address;
}
} else {
this.address = this.$route.params["address"];
}
localStorage.address = this.address;
},
nav() {
this.$get(
"class/getList", {},
res => {
console.log(res);
for(var i = 0; i < res.msg.length; i++) {
res.msg[i].isOpen = i == 0;
res.msg[0].show = i == 1;

res.msg[i].hide = i == 0;
res.msg[0].hide = i == 1;
}
this.navList = res.msg;
console.log("首页", this.navList)
}
);
}
},
data() {
return {
address: "上海",
model: 1,
navList: [{
title: "首页",
active: "active",
hide: "hide",
show: "show",
dataIndex: 1
},
{
title: "美妆护肤",
dataIndex: 2
},
{
title: "个护清洁",
dataIndex: 3
},
{
title: "母婴健康",
dataIndex: 4
},
{
title: "生活日用",
dataIndex: 5
},
{
title: "布艺家纺",
dataIndex: 6
},
{
title: "精油香薰",
dataIndex: 7
}
],
goodsList: [{}],
};
},
};



