GridItem
},
data() {
return {
title1: '精选直供',
title2: '甄选直订',
title3: '共享续杯',
title4: '会员福利',
title5: '分类'
};
},
methods: {

toGoodsList(title1) {
console.log("第一个", title1)
this.$router.push({
path: '/goodsList/goodsList',
name: '/goodsList/goodsList',
params: {
title: title1
}
})
},

toPick() {
console.log("去,甄选直订");
this.$router.push({
path: '/goodsList/goodsList'
})
},

toRefill() {
console.log("去,共享续杯");
this.$router.push({
path: '/goodsList/goodsList'
})
},

toMember() {
console.log("去,会员福利");
this.$router.push({
path: '/goodsList/goodsList'
})
},

toClass() {
console.log("去,分类");
this.$router.push({
path: '/class/class'
})
}
},
};



