
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

export default {
components: {
Flexbox,
FlexboxItem
},
props: ['goods'],
data() {
return {
list: []
};
},
mounted() {
this.update();
},
methods: {
update() {},
toGoodsInfo(goods) {
var goods_id = goods.goods_id;
this.$router.push({
path: "/goodsInfo/goodsInfo",
name: '/goodsInfo/goodsInfo',
params: {
goods_id: goods_id
}
});
}
}
};



