
import Swiper from 'vux/src/components/swiper/swiper.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'


import goodsCard from '../goodsCard/goodsCard.vue'
const baseList = [{}, {}, {}, {}]
const urlList = baseList.map((item, index) => ({
url: 'http://m.baidu.com',
img: item.img,
fallbackImg: item.fallbackImg,
}))
export default {
components: {
Swiper,
Flexbox,
FlexboxItem,
ViewBox,
goodsCard
},
ready() {},
methods: {},
mounted() {

this.$get(
this.$serverHome + "goods/getList", {
page: this.currentPage,
limit: this.pageSize
},
res => {
console.log(res);
this.list = res.msg;
}
);

},
data() {
return {
swiperList: urlList,
list: [{}, {}, {}, {}, {}, {}, {}, {}, {}]
}
}
}



