
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Marquee from 'vux/src/components/marquee/marquee.vue'
import MarqueeItem from 'vux/src/components/marquee/marquee-item.vue'

export default {
components: {
Marquee,
MarqueeItem,
Group,
Cell
},
mounted() {
setTimeout(() => {
this.asyncCount = 5
}, 1000);
this.update();
},
methods: {

toInfo(list) {
var goods_id = list.goods_id;
this.$router.push({
path: "/goodsInfo/goodsInfo",
name: '/goodsInfo/goodsInfo',
params: {
goods_id: goods_id
}
});
},
update() {
this.$get(
this.$serverHome + "goods/getList", {
page: this.currentPage,
limit: this.pageSize
},
res => {

this.list = res.msg;
}
);
}
},
data() {
return {
asyncCount: 0,
list: []
}
}
}



